h1 {
  text-align: center;
  font-family: cursive, Arial, Helvetica, sans-serif;
  color: rgb(221, 248, 103);
  font-weight: 700;
  font-size: 50px;
  font-style: italic;
  /* margin-top: 50px; */
  margin-bottom: 5px;
  letter-spacing: 2px;
  filter: blur(1px);
  text-shadow: 0 0 30px rgb(255, 45, 45);
}

button {
  background-color: rgba(227, 235, 208, 0.7);
  padding: 3px 10px;
  margin: 5px 2px;
  font-family: fantasy;
  color: rgb(32, 30, 30);
  opacity: 0.8;
  font-weight: 300;
  word-spacing: 3px;
}
select {
  margin: 0 3px;
}

.btns {
  text-align: center;
  margin: 0;
}
