.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.displaybox {
  display: flex;
  /* align-items: center; */
  /* text-align: center; */
  justify-content: center;
  background-color: rgb(32, 30, 30);
  height: 500px;
  width: 95%;
  margin: auto;
  margin-top: 30px;
}

tr {
  height: 50px;
  width: 50px;
  /* margin: auto; */
}

tbody {
  display: inline-block;
}
